<template>
    <div class="myCat">

           <!-- 顶部图片 -->
           <div class="topImg"></div>
           
            <!-- 商品清单 -->
            <div class="catBody" ref="catBody" v-if="isBodys">
                    <div class="Tops">
                        <span>食品与小食订单</span>
                        <span>自提/外送</span>
                        <span @click="ff_Allrovems">
                            <span class="iconfont icon-lajitong"></span>
                            清空
                        </span>
                    </div>
                    <div class="Bodys">
                        
                                <van-swipe-cell v-for="(item,index) in Dates" :key="index" :disabled="!item.ischeack">
                                    <van-checkbox v-model="item.ischeack" @change="ff_cheacks" icon-size="22px" checked-color="#6d87c4" class="myCheck"></van-checkbox>
                                            <van-card
                                                :price="item.price"
                                                :desc="item.rule"
                                                :title="item.name"
                                                class="goods-card"
                                                :thumb="item.small_img"
                                            />
                                            <span class="hots" v-if="item.isHots">Hots</span>
                                            <span class="original">{{item.price-0+18}}</span>
                                            <van-stepper v-model="item.count" theme="round" button-size="22" disable-input @change="ff_addCount(item)"/>
                                            <template #right >
                                                <van-button  @click="ff_rovemCount(item)"  square text="删除" type="danger" class="delete-button" />
                                            </template>
                                </van-swipe-cell>
                    </div>
                    <div class="tail" :class="{'newTail' : isSocll}" ref="tail">
                            <div class="lefts">
                                <p class="join">合计<span>￥{{priceObj.newDate}}</span><span>￥{{priceObj.costDate}}</span></p>
                                <van-checkbox checked-color="#6d87c4" v-model="isCheck" @click="ff_Allcheack(isCheck)"  icon-size="24px">全选</van-checkbox>
                            </div>
                            <div class="rights">
                                <p @click="goPath('Pay')">优惠后￥{{priceObj.newDate}},去结算</p>
                                <p>已享受更低优惠,优惠后共减免￥{{(priceObj.costDate-0) - (priceObj.newDate-0)}}</p>
                            </div>
                    </div>
            </div>

            <!-- 空空如也 -->
            <div class="kong" v-if="!isBodys">
                    <router-link class="buttons" :to="{name:'Menu'}">去喝一杯</router-link>
            </div>

           <Guess :newDates="newLis" @gai="ff_gai($event)"></Guess>
           
    </div>
</template>

<script>
    import '../assets/less/cat.less'
    import '../assets/font/iconfont.css'
    import '../assets/less/cat.less'
    import Guess from '../components/Guess.vue'
    export default {
        name:'Cat',
        data() {
            return {
                Dates          : [],
                newLis         : [],
                priceObj       : {
                    // 原价
                    costDate   : 0,
                    // 现价
                    newDate    : 0,
                    // 终价
                    ultiDate   : 0,
                    // 终极差价
                    ultiDiffer : 0,
                },
                isCheck        : true,
                oldCheck       : [],
                isBodys        : false,
                isSocll        : false,
                
                
            }
        },

        created() {
            
            if(this.$route.query.isPay){
                  
                // this.goPath('Pay',);
            }
            this.$toast.loading({
                message: '加载中...',
                forbidClick: true,
                loadingType: 'spinner',
                });

            if(this.useId){
                // 购物车数据请求
               this.ff_shopCats();
            }

            // 请求猜你喜欢数据
            this.ff_Like();
        },

        // 购物车的结算滑块
        mounted(){
            setTimeout(()=>{
                this.ff_huaK();
            },500) 
        },
       
       
        methods: {
            // 全选逻辑
            ff_Allcheack(){           
                for(let i=0;i<this.Dates.length;i++){
                    this.Dates[i].ischeack = this.isCheck;
                }  
                this.ff_sum();
            },
            // 单选逻辑
            ff_cheacks(){
                
                 for(let i=0;i<this.Dates.length;i++){
                    this.oldCheck.push(this.Dates[i].ischeack)
                 }  
                 
                 this.ff_sum();

                 for(let i=0;i<this.Dates.length;i++){
                       if(!this.Dates[i].ischeack){
                             this.isCheck = false;
                             return;
                       } 
                 }
                 this.isCheck = true;
            },
             // 修改购物车商品数量
            ff_addCount(Item){
                this.axios({
                    url     : '/modifyShopcartCount',
                    method  : 'POST',
                    data    : {
                        appkey: this.appkey,
                        tokenString: this.useId,
                        sid: Item.sid,
                        count: Item.count
                    }
                })
                .then(res=>{
                    if(res.data.code == 6000){
                          this.ff_sum();
                    }
                })
                .catch(err=>{
                    
                })
            },
            ff_rovemCount(Item){
                let dateArr = [];
               
                dateArr.push(Item.sid);
                
                let sids = JSON.stringify(dateArr);
                
                this.ff_rovems(sids);
               
                
                

            },
            // 计算商品总价
            ff_sum(){
                 let  prices =  0;
                 let  cost   =  0;

                 this.Dates.forEach(Item=>{
                     if(Item.ischeack){
                         prices += (Item.price-0)    * (Item.count-0);
                         cost   += (Item.price-0+16) * (Item.count-0);
                     }
                 });
                 this.priceObj.costDate    = cost;
                 this.priceObj.newDate     = prices;
                 this.priceObj.ultiDate    = prices==0 ? 0 : prices -25;
                 this.priceObj.ultiDiffer  = cost - this.priceObj.ultiDate;

                 
                 
            },
            //  购物车数据请求
            ff_shopCats(){
                 this.axios({
                        url:'/findAllShopcart',
                        method:'GET',
                        params:{
                            appkey      : this.appkey,
                            tokenString : this.useId,
                        }
                        })
                        .then(res=>{
                            if(res.data.code == 5000){                                
                                let testObj = res.data.result;
                                testObj.forEach((item,index)=>{
                                    item.ischeack = true;
                                    item.isHots = Math.floor(Math.random()*10) > 5 ? true : false;
                                    
                                })
                                this.Dates = testObj;
                                 

                                this.ff_sum();
                                
                              
                              
                                
                                if(this.Dates.length == 0){

                                    this.isBodys = false;
                                }else{
                                    this.isBodys = true;
                                }
                                
                                
                            }
                })
                .catch(err=>{
                     
                });
            },
            // 全部删除逻辑
            ff_Allrovems(){
                let dateArr = [];
                this.Dates.forEach(Item=>{
                    if(Item.ischeack){
                        dateArr.push(Item.sid);
                    }
                    
                    
                });
                
                let sids = JSON.stringify(dateArr);
                
                this.ff_rovems(sids);
           
            
             },
            //  删除数据的逻辑
            ff_rovems(sids){
                  if(this.useId){
                    this.axios({
                        url : '/deleteShopcart',
                        method : 'POST',
                        data : {
                            appkey      : this.appkey,
                            tokenString : this.useId,
                            sids,

                        }
                        })
                        .then(res=>{
                            if(res.data.code == 7000){

                                this.ff_shopCats(); 
                              
                                this.ff_huaK();

                             
                                
                               
                            }
                        })
                        .catch(err=>{
                             
                        })

                }else{
                        this.$toast('请先登录');
                        setTimeout(()=>{
                            this.$router.push('Login');
                        },500)
                    }

             },
            //  更改猜你喜欢数据
            ff_gai(obj){
                this.ff_Like(obj.key,obj.type)

            },
            // 猜你喜欢数据请求
            ff_Like(key='isHot',value=1){
                    this.axios({
                            url:'/typeProducts',
                            method:'GET',
                            params:{
                                appkey : this.appkey,
                                key,
                                value
                            }
                            })
                            .then(res=>{
                                this.newLis = res.data.result;
                            })
                            .catch(err=>{
                                 
                            })
            },
            // 结算滑块
            ff_huaK(){
                let catBody = this.$refs.catBody;
                if(catBody){
                    
                    let tail    = this.$refs.tail;
                    let Hegits  = catBody.offsetHeight+40-document.body.clientHeight-40;
                    //  
                    if(tail){
                            let strTop = tail.offsetTop;
                            if(strTop >= document.body.clientHeight-40){
                                    this.isSocll = true;
                            }else{
                                    this.isSocll = false;
                            }

                            window.addEventListener("scroll",(e)=>{
                                    if(e.path[1].pageYOffset<Hegits){
                                        this.isSocll = true;
                                    }else{
                                        this.isSocll = false;
                                    }
                            });
                    }

                  }
            },
            // 跳转模块
            goPath(name){
                  let sidx = [];
                  this.Dates.forEach(Item=>{
                      if(Item.ischeack){
                         sidx.push(Item.sid)
                      } 
                  })
                  let sids =  JSON.stringify(sidx);
                  this.$router.push({name,query:{sids}});  
            }
        },
        components:{
            Guess,
        }
    }
</script>

<style lang="less" scoped>
     
</style>